@import "../define";
@import "../mixins/input";

@input-cls: ~"@{css-prefix}input";

.@{input-cls} {
  border: 1px solid @color-font-02;
  outline: 0 !important;
  color: @color-font-04;
  background: @color-inverse;
  width: @input-width;
  .transition;

  &:active, &.active, &:hover , &:focus {
    border-color: @color-primary;
  }
  .border-radius;
  .placeholder;
  .input-md;

  &.input-xs {
    .input-xs;
  }
  &.input-sm {
    .input-sm;
  }
  &.input-md {
    .input-md;
  }
  &.input-lg {
    .input-lg;
  }
  &.input-disabled, &[disabled], &.disabled {

  }
}