@import "../define";
@import "../mixins/autocomplete";

@index-prompt: 900;

.@{autocomplete-cls} {
  background: white;
  border: 1px solid @color-font-02;
  position: relative;
  & > *:not(input):not(ul) {
    float: left;
    color: @color-font-03;
    fill: @color-font-03;
    display: none;
    &:first-child {
      display: block;
    }
    &.autocomplete-clickable {
      cursor: pointer;
    }
  }
  & > input[type=text] {
    padding: 0 6px;
    height: 100%;
    border: 0 !important;
    outline: none !important;
    float: left;
    background: transparent;
    color: @color-font-05;
    .placeholder();
  }
  &.has-value {
    & > *:not(input) {
      display: block;
    }
  }
  &:hover {
    border-color: @color-primary;
  }
  svg:hover {
    fill: @color-primary;
  }
}

ul.autocomplete-prompt {
  position: absolute;
  z-index: @index-prompt;
  border: @divider-default;
  list-style: none;
  padding: 0;
  overflow: auto;
  background: white;
  li {
    list-style: none;
    padding: 0 8px;
    word-break: keep-all;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    color: @color-font-04;
    span {
      word-break: keep-all;
      white-space: nowrap;
    }
    &.on {
      background: @color-general-ac05;
    }
    &:hover {
      background: @color-general-ac06;
    }
    &.no-data {
      cursor: default;
    }
  }
  &:empty {
    display: none;
  }
}

.autocomplete-size;

.autocomplete-xs {
  .autocomplete-size(@main-height:24px, @main-width: 200px, @main-font-size:12px, @icon-font-size:12px, @icon-size:16px, @icon-gutter:8px,@item-height:24px, @item-font-size:12px);
}

.autocomplete-sm {
  .autocomplete-size;
}

.autocomplete-md {
  .autocomplete-size(@main-height:40px, @main-width: 240px, @main-font-size:14px, @icon-font-size:16px, @icon-size:20px, @icon-gutter:8px,@item-height:32px, @item-font-size:14px);
}

.autocomplete-lg {
  .autocomplete-size(@main-height:48px, @main-width: 320px, @main-font-size:18px, @icon-font-size:18px, @icon-size:24px, @icon-gutter:8px,@item-height:32px, @item-font-size:14px);
}

.autocomplete-theme-normal {
  .normal-prompt();
}