@import "../define";
@import "../mixins/spin";

@message-cls: ~"@{css-prefix}message";
@index-message: 1000;
@message-color: @color-general-ac01;

.@{message-cls} {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  z-index: @index-message;
  text-align: center;
  .message-content-container {
    pointer-events: none;
    .inverse {
      .transform(rotate(180deg));
    }
    &.message-info {
      .message-flag-circle {
        background-color: @color-info;
      }
    }
    &.message-error {
      .message-flag-circle {
        background-color: @color-error;
      }
    }
    &.message-success {
      .message-flag-circle {
        background-color: @color-success;
      }
    }
    &.message-warn {
      .message-flag-circle {
        background-color: @color-warn;
      }
    }
  }
  .message-content {
    text-align: left;
    background: @message-color;
    position: relative;
    .border-radius;
    margin: 16px auto 0;
    padding: 16px 32px;
    max-width: 600px;
    min-width: 200px;
    width: auto;
    line-height: 24px;
    display: inline-block;
    pointer-events: all;
    //.box-shadow-3;
    .transform(translateY(-50%));
    .transition(@duration: 0.3s, @timing: linear);
    opacity: 0;
    &.message-show {
      .transform(translateY(0));
      opacity: 0.95;
    }
    .message-flag {
      position: absolute;
      left: 32px;
      width: 24px;
      height: 24px;
      & ~ .message-text {
        padding-left: 40px;
      }
    }
    .message-flag-circle {
      border-radius: 50%;
      font-size: 20px;
      color: @color-font-05-inverse;
      text-align: center;
      line-height: 20px;
    }
    .@{spin-cls} {
      .icon-spin(@color-info, 100%);
    }
    .message-text {
      .font-title;
      font-weight: normal;
      color: @color-font-05-inverse;
    }
    .message-close,.message-countdown {
      position: absolute;
      right: 16px;
      top: 16px;
      & ~ .message-text {
        padding-right: 60px;
      }
    }
    .message-close {
      font-size: 18px;
      color: @color-font-03-inverse;
      cursor: pointer;
      &:hover {
        color: @color-font-05-inverse;
      }
    }
    .message-countdown {
      .font-title;
      font-weight: normal;
      color: @color-font-05-inverse;
      width: 28px;
      .line-hidden;
      text-align: right;
    }
  }
  &.message-right {
    text-align: right;
    .message-content {
      margin-left: 0;
      margin-right: 16px;
    }
  }
  &.message-left {
    text-align: left;
    .message-content {
      margin-left: 16px;
      margin-right: 0;
    }
  }
}
