@import "../define";

@notification-cls: ~"@{css-prefix}notification";
@index-notification: 1000;
@notification-color: white;

.@{notification-cls} {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  z-index: @index-notification;
  text-align: center;
  .notification-content-container {
    pointer-events: none;
    &.notification-info {
      .notification-flag {
        color: @color-info;
      }
    }
    &.notification-error {
      .notification-flag {
        color: @color-error;
      }
    }
    &.notification-success {
      .notification-flag {
        color: @color-success;
      }
    }
    &.notification-warn {
      .notification-flag {
        color: @color-warn;
      }
    }
  }
  .notification-content {
    text-align: left;
    background: @notification-color;
    position: relative;
    .border-radius;
    margin: 16px auto 0;
    padding: 8px 8px;
    max-width: 600px;
    min-width: 200px;
    width: auto;
    line-height: 24px;
    display: inline-block;
    pointer-events: all;
    .box-shadow-3;
    .transform(translateX(20%));
    opacity: 0;
    &.notification-show {
      .transform(translateX(0))!important;
      .transition(@duration: 0.3s, @timing: linear);
      opacity: 1;
    }
    .notification-flag {
      position: absolute;
      top: 12px;
      left: 8px;
      & > *{
        font-size: 32px;
        max-width: 32px;
        max-height: 32px;
      }
      &:not(:empty) ~ .notification-text {
        padding-left: 40px;
      }
    }
    .notification-text {
      padding-right: 24px;
      .notification-title {
        .font-title;
      }
      .notification-desc {
        .font-body-1;
      }
    }
    .notification-close {
      position: absolute;
      right: 8px;
      top: 8px;
      cursor: pointer;
    }
  }
  &.notification-bottomRight {
    text-align: right;
    height: auto;
    top:auto;
    bottom: 0;
    pointer-events: none;
    .notification-content {
      margin:0 16px 16px 0;
    }
  }
  &.notification-bottomLeft {
    text-align: left;
    height: auto;
    top:auto;
    bottom: 0;
    pointer-events: none;
    .notification-content {
      margin:0 0 16px 16px;
      .transform(translateX(-20%));
    }
  }
  &.notification-topRight {
    text-align: right;
    .notification-content {
      margin-left: 0;
      margin-right: 16px;
    }
  }
  &.notification-topLeft {
    text-align: left;
    .notification-content {
      margin-left: 16px;
      margin-right: 0;
      .transform(translateX(-20%));
    }
  }
}
