.font-size(@size,@line-height:@line-height) {
  font-size: @size;
  line-height: @line-height;
}

.font-body-1() {
  .font-size(14px);
  font-weight: normal;
  color: @color-font-04;
}

.font-body-2() {
  .font-size(14px);
  font-weight: normal;
  color: @color-font-03;
}

.font-body-3() {
  .font-size(14px);
  font-weight: bold;
  color: @color-font-05;
}

.font-caption() {
  .font-size(12px);
  font-weight: normal;
  color: @color-font-03;
}

.font-disable() {
  .font-size(12px);
  font-weight: normal;
  color: @color-font-02;
}

.font-title() {
  .font-size(16px);
  font-weight: bold;
  color: @color-font-05;
}

.font-display-1() {
  .font-size(18px);
  font-weight: bold;
  color: @color-font-05;
}

.font-display-2() {
  .font-size(24px);
  font-weight: bold;
  color: @color-font-05;
}

.font-display-3() {
  .font-size(36px);
  font-weight: bold;
  color: @color-font-05;
}

.box-shadow(@x:0,@y:1px,@d:1px, @e:0,@color: rgba(0, 0, 0, .1)) {
  box-shadow: @arguments;
}

.box-shadow-1() {
  .box-shadow;
}

.box-shadow-2() {
  .box-shadow(0, 1px, 3px, 0, rgba(0, 0, 0, .2));
}

.box-shadow-3() {
  .box-shadow(0, 1px, 8px, 2px, rgba(0, 0, 0, .3));
}

.text-shadow(@x:0,@y:1px,@d:1px, @color: rgba(0, 0, 0, .1)) {
  text-shadow: @arguments;
}

.text-shadow-1() {
  .text-shadow;
}

.text-shadow-2() {
  .text-shadow(0, 1px, 3px, rgba(0, 0, 0, .2));
}

.text-shadow-3() {
  .text-shadow(0, 1px, 8px, rgba(0, 0, 0, .3));
}

.border-radius(@lt:@border-radius,@rt:@border-radius,@rb:@border-radius,@lb:@border-radius) {
  border-radius: @arguments;
}

@transition-duration: .5s;

@transition-timing: cubic-bezier(.25, .8, .25, 1);

.transition(@property:all,@duration:@transition-duration,@timing:@transition-timing,@delay:0s) {
  -webkit-transition: @arguments;
  -moz-transition: @arguments;
  -o-transition: @arguments;
  transition: @arguments;
}

.transform(@transform) {
  -webkit-transform: @transform;
  -moz-transform: @transform;
  -ms-transform: @transform;
  transform: @transform;
}

.scroll(@bg: @color-general-ac06,@thumb: @color-general-ac03,@btn: @color-general-ac03) {
  scrollbar-3dlight-color: @bg;
  scrollbar-highlight-color: @bg;
  scrollbar-face-color: @thumb;
  scrollbar-arrow-color: @btn;
  scrollbar-shadow-color: @thumb;
  scrollbar-dark-shadow-color: @thumb;
  scrollbar-base-color: @bg;
  scrollbar-track-color: @bg;
  overflow: auto;
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
    background: transparent;
  }
  &::-webkit-scrollbar-button {
    background-color: @btn;
    display: none;
  }
  &::-webkit-scrollbar-track {
    background: @bg;
  }
  &::-webkit-scrollbar-track-piece {
    background: @bg;
  }
  &::-webkit-scrollbar-thumb {
    background: @thumb;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-corner {
    background: @bg;
  }
  &::-webkit-resizer {
    background: @bg;
  }
}

.size(@size) {
  width: @size;
  height: @size;
}

.spin-keyframes() {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}

.line-hidden() {
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.disabled() {
  opacity: .3;
  cursor: not-allowed;
}

.placeholder(@color: @color-font-02) {
  &::-moz-placeholder {
    color: @color;
    opacity: 1;
  }
  &:-ms-input-placeholder {
    color: @color;
  }
  &::-webkit-input-placeholder {
    color: @color;
  }
}

.clearfix() {
  zoom: 1;
  &:before,
  &:after {
    content: "";
    display: table;
  }
  &:after {
    clear: both;
    visibility: hidden;
    font-size: 0;
    height: 0;
  }
}

.center-block() {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.hide-text() {
  font: ~"0/0" a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

.text-hide() {
  .hide-text();
}

