@import "../define";

@modal-cls: ~"@{css-prefix}modal";
@modal-perspective-cls: ~"@{css-prefix}modal-perspective";
@modal-backdrop-cls: ~"@{css-prefix}modal-backdrop";
@modal-effect-att: ~"@{css-prefix}modal-effect";
@index-modal-backdrop: 999;
@index-modal: @index-modal-backdrop + 1;
@modal-color: white;

.transform-style(@transform:preserve-3d) {
  -webkit-transform-style: @transform;
  -moz-transform-style: @transform;
  transform-style: @transform;
}

.transform-origin(@transform) {
  -webkit-transform-origin: @transform;
  -moz-transform-origin: @transform;
  transform-origin: @transform;
}

.perspective(@perspective) {
  -webkit-perspective: @perspective;
  -moz-perspective: @perspective;
  perspective: @perspective;
}

@-webkit-keyframes slit {
  50% {
    -webkit-transform: translateZ(-250px) rotateY(89deg);
    opacity: 1;
    -webkit-animation-timing-function: ease-in;
  }
  100% {
    -webkit-transform: translateZ(0) rotateY(0deg);
    opacity: 1;
  }
}

@-moz-keyframes slit {
  50% {
    -moz-transform: translateZ(-250px) rotateY(89deg);
    opacity: 1;
    -moz-animation-timing-function: ease-in;
  }
  100% {
    -moz-transform: translateZ(0) rotateY(0deg);
    opacity: 1;
  }
}

@keyframes slit {
  50% {
    transform: translateZ(-250px) rotateY(89deg);
    opacity: 1;
    animation-timing-function: ease-in;
  }
  100% {
    transform: translateZ(0) rotateY(0deg);
    opacity: 1;
  }
}

@-webkit-keyframes rotateRightSideFirst {
  50% {
    -webkit-transform: translateZ(-50px) rotateY(5deg);
    -webkit-animation-timing-function: ease-out;
  }
  100% {
    -webkit-transform: translateZ(-200px);
  }
}

@-moz-keyframes rotateRightSideFirst {
  50% {
    -moz-transform: translateZ(-50px) rotateY(5deg);
    -moz-animation-timing-function: ease-out;
  }
  100% {
    -moz-transform: translateZ(-200px);
  }
}

@keyframes rotateRightSideFirst {
  50% {
    transform: translateZ(-50px) rotateY(5deg);
    animation-timing-function: ease-out;
  }
  100% {
    transform: translateZ(-200px);
  }
}

@-webkit-keyframes OpenTop {
  50% {
    -webkit-transform: rotateX(10deg);
    -webkit-animation-timing-function: ease-out;
  }
}

@-moz-keyframes OpenTop {
  50% {
    -moz-transform: rotateX(10deg);
    -moz-animation-timing-function: ease-out;
  }
}

@keyframes OpenTop {
  50% {
    transform: rotateX(10deg);
    animation-timing-function: ease-out;
  }
}

.@{modal-perspective-cls} {
  &, & body {
    height: 100%;
    overflow: hidden;
  }
  & body {
    -webkit-perspective: 600px;
    -moz-perspective: 600px;
    perspective: 600px;
    background: #222;
  }
}

.@{modal-cls} {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 50%;
  max-width: 600px;
  min-width: 300px;
  height: auto;
  z-index: @index-modal;
  visibility: hidden;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  .transform(translateX(-50%) translateY(-50%));
  &.modal-xl{
    @width: 1500px;
    width: @width;
    max-width: @width;
  }
  &.modal-lg{
    @width: 900px;
    width: @width;
    max-width: @width;
  }
  &.modal-md{
    @width: 600px;
    width: @width;
    max-width: @width;
  }
  &.modal-sm{
    @width: 300px;
    width: @width;
    max-width: @width;
  }
  &.modal-xs{
    @width: 200px;
    width: @width;
    max-width: @width;
  }
  &.modal-show {
    visibility: visible;
    & ~ .@{modal-backdrop-cls} {
      opacity: 1;
      visibility: visible;
    }
  }
  .modal-content {
    background: @modal-color;
    position: relative;
    .border-radius;
    margin: 0 auto;
    .box-shadow-3;
    @gutter: 24px;
    .modal-head {
      @head-height: 48px;
      height: @head-height;
      padding: 0 @gutter;
      border-bottom: @divider-default;
      .modal-title {
        .font-title;
        width: calc(~"100% - @{gutter}");
        float: left;
        line-height: @head-height;
        .line-hidden;
      }
      i.modal-close {
        float: right;
        font-size: 24px;
        line-height: @head-height;
        cursor: pointer;
        &:hover {
          color: @color-primary;
        }
      }
    }
    .modal-body {
      padding: @gutter;
      min-height: 160px;
    }
    .modal-foot {
      padding: 0 @gutter @gutter;
      text-align: right;
      button:not(:first-child) {
        margin-left: 13px;
      }
    }
  }

  &:not([@{modal-effect-att}]) {
    .modal-content {
      opacity: 0;
      .transition;
    }
    &.modal-show .modal-content {
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='0'] {
    .modal-content {
      transform-origin: 0 0;
      opacity: 0;
      .transition;
    }
    &.modal-show .modal-content {
      .transform(translateX(0) translateY(0) scale(1))!important;
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='1'] {
    .modal-content {
      .transform(scale(0.7));
      opacity: 0;
      .transition(@duration: 0.3s, @timing: linear);
    }
    &.modal-show .modal-content {
      .transform(scale(1));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='2'] {
    .modal-content {
      .transform(translateX(20%));
      opacity: 0;
      .transition(@duration: 0.3s, @timing: cubic-bezier(0.25, 0.5, 0.5, 0.9));
    }
    &.modal-show .modal-content {
      .transform(translateX(0));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='3'] {
    .modal-content {
      .transform(translateY(20%));
      opacity: 0;
      .transition(@duration: 0.3s, @timing: linear);
    }
    &.modal-show .modal-content {
      .transform(translateY(0));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='4'] {
    .modal-content {
      .transform(scale(0) rotate(720deg));
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transform(scale(1) rotate(0deg));
      opacity: 1;
    }
    .modal-content, & ~ .@{modal-backdrop-cls} {
      .transition(@duration: 0.5s,@timing: linear);
    }
  }

  &[@{modal-effect-att}='5'] {
    .perspective(1300px);
    .modal-content {
      .transform-style;
      .transform(translateZ(600px) rotateX(20deg));
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transition(@duration: 0.3s, @timing: ease-in);
      .transform(translateZ(0px) rotateX(0deg));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='6'] {
    .perspective(1300px);
    .modal-content {
      .transform-style;
      .transform(translate(30%) translateZ(600px) rotate(10deg));
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transition(@duration: 0.3s, @timing: ease-in);
      .transform(translate(0%) translateZ(0) rotate(0deg));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='7'] {
    top: 0;
    .transform(translateX(-50%));
    .modal-content {
      .transform(translateY(-200%));
      .transition(@duration: 0.3s, @timing: linear);
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transform(translateY(0%));
      border-radius: 0 0 3px 3px;
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='8'] {
    .perspective(1300px);
    .modal-content {
      .transform-style;
      .transform(rotateY(-70deg));
      .transition(@duration: 0.3s, @timing: linear);
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transform(rotateY(0deg));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='9'] {
    .perspective(1300px);
    .modal-content {
      .transform-style;
      .transform(rotateX(-70deg));
      .transition(@duration: 0.3s, @timing: linear);
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transform(rotateX(0deg));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='10'] {
    .perspective(1300px);
    .modal-content {
      .transform-style;
      .transform(rotateX(-60deg));
      .transform-origin(50% 0);
      .transition(@duration: 0.3s, @timing: linear);
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transform(rotateX(0deg));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='11'] {
    .modal-content {
      .transform(scale(2));
      .transition(@duration: 0.3s, @timing: linear);
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transform(scale(1));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='12'] {
    .modal-content {
      background: transparent;
      .transform(scale(0.8));
      .transition(@duration: 0.3s, @timing: linear);
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transform(scale(1));
      opacity: 1;
    }
    & ~ .@{modal-backdrop-cls} {
      background: @modal-color;
    }
  }

  &[@{modal-effect-att}='13'] {
    .perspective(1300px);
    .modal-content {
      .transform-style;
      .transform(translateZ(-3000px) rotateY(90deg));
      opacity: 0;
    }
    &.modal-show .modal-content {
      -webkit-animation: slit .7s forwards ease-out;
      -moz-animation: slit .7s forwards ease-out;
      animation: slit .7s forwards ease-out;
    }
  }

  &[@{modal-effect-att}='14'] {
    .perspective(1300px);
    .modal-content {
      .transform-style;
      .transform(translateY(100%) rotateX(90deg));
      .transform-origin(0 100%);
      .transition(@duration: 0.3s, @timing: ease-out);
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transform(translateY(0%) rotateX(0deg));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='15'] {
    .perspective(1300px);
    .modal-content {
      .transform-style;
      .transform(translateZ(100px) translateX(-30%) rotateY(90deg));
      .transform-origin(0 100%);
      .transition(@duration: 0.3s, @timing: linear);
      opacity: 0;
    }
    &.modal-show .modal-content {
      .transform(translateZ(0px) translateX(0%) rotateY(0deg));
      opacity: 1;
    }
  }

  &[@{modal-effect-att}='16'] {
    .modal-content {
      .transform(translateY(-5%));
      .transition(@duration: 0.3s, @timing: linear);
      opacity: 0;
    }
    &.modal-show {
      & ~ .modal-outside-container {
        -webkit-filter: blur(3px);
        -moz-filter: blur(3px);
        filter: blur(3px);
        .transition(@duration: 0.3s, @timing: linear);
      }
      .modal-content {
        .transform(translateY(0));
        opacity: 1;
      }
    }
    & ~ .@{modal-backdrop-cls} {
      background: fade(@modal-color, 50%);
    }
  }

  &[@{modal-effect-att}='17'] {
    .modal-content {
      .transform(translateY(200%));
      opacity: 0;
    }
    &.modal-show {
      & ~ .modal-outside-container {
        height: 100%;
        overflow: hidden;
        background: @body-background;
        -webkit-transition: -webkit-transform 0.3s;
        -moz-transition: -moz-transform 0.3s;
        transition: transform 0.3s;
      }
      .modal-content {
        .transform(translateY(0));
        .transition(@duration: 0.3s, @timing: linear, @delay: 0.2s);
        opacity: 1;
      }
      & ~ .modal-outside-container, & ~ .@{modal-backdrop-cls} {
        .transform(rotateX(-2deg));
        .transform-origin(50% 0%);
        .transform-style;
      }
    }
  }

  &[@{modal-effect-att}='18'] {
    .modal-content {
      .transform(translateX(200%));
      opacity: 0;
    }
    &.modal-show {
      & ~ .modal-outside-container {
        height: 100%;
        overflow: hidden;
        background: @body-background;
      }
      .modal-content {
        .transform(translateX(0));
        .transition(@duration: 0.5s, @timing: linear, @delay: 0.1s);
        opacity: 1;
      }
      & ~ .modal-outside-container, & ~ .@{modal-backdrop-cls} {
        -webkit-animation: rotateRightSideFirst 0.5s forwards ease-in;
        -moz-animation: rotateRightSideFirst 0.5s forwards ease-in;
        animation: rotateRightSideFirst 0.5s forwards ease-in;
        .transform-origin(0% 50%);
        .transform-style;
      }
      & ~ .@{modal-backdrop-cls} {
        background: fade(@modal-color, 80%);
        .transition(@duration: 0.5s, @timing: linear);
      }
    }
  }

  &[@{modal-effect-att}='19'] {
    .modal-content {
      .transform(translateY(-200%));
      opacity: 0;
    }
    &.modal-show {
      & ~ .modal-outside-container {
        height: 100%;
        overflow: hidden;
        background: @body-background;
      }
      .modal-content {
        .transform(translateY(0));
        .transition(@duration: 0.5s, @timing: linear, @delay: 0.1s);
        opacity: 1;
      }
      & ~ .modal-outside-container, & ~ .@{modal-backdrop-cls} {
        -webkit-animation: OpenTop 0.5s forwards ease-in;
        -moz-animation: OpenTop 0.5s forwards ease-in;
        animation: OpenTop 0.5s forwards ease-in;
        .transform-origin(50% 100%);
        .transform-style;
      }
      & ~ .@{modal-backdrop-cls} {
        .transition(@duration: 0.5s, @timing: linear);
      }
    }
  }
}

.@{modal-backdrop-cls} {
  position: fixed;
  width: 100%;
  height: 100%;
  visibility: hidden;
  top: 0;
  left: 0;
  z-index: @index-modal-backdrop;
  opacity: 0;
  .transition;
}
