@import "../define";
@import "../mixins/spin";

@tooltip-cls: ~"@{css-prefix}tooltip";
@index-tooltip: 1060;
@tooltip-color: @color-general-ac01;
@arrow-size: 5px;

.@{tooltip-cls} {
  .transition() {
    @transition: opacity @transition-duration @transition-timing,transform @transition-duration @transition-timing;
    -webkit-transition: @transition;
    -moz-transition: @transition;
    -o-transition: @transition;
    transition: @transition;
  }
  position: absolute;
  z-index: @index-tooltip;
  display: block;
  visibility: visible;
  opacity: 0;
  pointer-events: none;
  .transform(scale(0));
  .transition;
  .tooltip-content {
    .tooltip-arrow {
      position: absolute;
      width: 0;
      height: 0;
      border: @arrow-size solid transparent;
    }
    .tooltip-inner {
      padding: 8px 16px;
      line-height: 24px;
      color: @color-font-05-inverse;
      text-align: left;
      text-decoration: none;
      background-color: @tooltip-color;
      .border-radius;
      .tooltip-loading{
        vertical-align: -5px;
        .@{spin-cls} {
          .icon-spin(@color-info, 100%);
        }
      }
      //.transition;
    }
  }
  &.tooltip-show{
    opacity: 1;
    .transform(scale(1));
    pointer-events: all;
  }
  @gutter: 8px;
  @side-offsetH: 16px;
  @side-offsetV: 8px;
  @oh: @side-offsetH + @arrow-size;
  @ov: @side-offsetV + @arrow-size;
  .tooltip-position(@side, @paddingSide) {
    padding-@{paddingSide}: @gutter;
    .tooltip-arrow {
        @{paddingSide}: @gutter - @arrow-size;
      border-@{paddingSide}-width: 0;
      border-@{side}-color: @tooltip-color;
    }
  }
  &[tooltip-position="top"] {
    transform-origin: 50% 100%;
    .tooltip-position(top, bottom);
    .tooltip-arrow {
      left: 50%;
      margin-left: -@arrow-size;
    }
  }
  &[tooltip-position="topLeft"] {
    transform-origin: @oh 100%;
    .tooltip-position(top, bottom);
    .tooltip-arrow {
      left: @side-offsetH;
    }
  }
  &[tooltip-position="topRight"] {
    transform-origin: calc(~"100% - @{oh}") 100%;
    .tooltip-position(top, bottom);
    .tooltip-arrow {
      right: @side-offsetH;
    }
  }
  &[tooltip-position="right"] {
    transform-origin: 0% 50%;
    .tooltip-position(right, left);
    .tooltip-arrow {
      top: 50%;
      margin-top: -@arrow-size;
    }
  }
  &[tooltip-position="rightTop"] {
    transform-origin: 0% @ov;
    .tooltip-position(right, left);
    .tooltip-arrow {
      top: @side-offsetV;
    }
  }
  &[tooltip-position="rightBottom"] {
    transform-origin: 0% calc(~"100% - @{ov}");
    .tooltip-position(right, left);
    .tooltip-arrow {
      bottom: @side-offsetV;
    }
  }
  &[tooltip-position="bottom"] {
    transform-origin: 50% 0%;
    .tooltip-position(bottom, top);
    .tooltip-arrow {
      left: 50%;
      margin-left: -@arrow-size;
    }
  }
  &[tooltip-position="bottomLeft"] {
    transform-origin: @oh 0%;
    .tooltip-position(bottom, top);
    .tooltip-arrow {
      left: @side-offsetH;
    }
  }
  &[tooltip-position="bottomRight"] {
    transform-origin: calc(~"100% - @{oh}") 0%;
    .tooltip-position(bottom, top);
    .tooltip-arrow {
      right: @side-offsetH;
    }
  }
  &[tooltip-position="left"] {
    transform-origin: 100% 50%;
    .tooltip-position(left, right);
    .tooltip-arrow {
      top: 50%;
      margin-top: -@arrow-size;
    }
  }
  &[tooltip-position="leftTop"] {
    transform-origin: 100% @ov;
    .tooltip-position(left, right);
    .tooltip-arrow {
      top: @side-offsetV;
    }
  }
  &[tooltip-position="leftBottom"] {
    transform-origin: 100% calc(~"100% - @{ov}");
    .tooltip-position(left, right);
    .tooltip-arrow {
      bottom: @side-offsetV;
    }
  }
}